import React, {useState} from 'react';
import ForgotPasswordImg from 'src/components/ForgotPasswordImg';
import { Container, Typography, TextField, Button, Grid, Box, colors, Paper, CssBaseline} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { Link } from "react-router-dom";
import { useDispatch } from "react-redux";
import { forgotPassWordAction } from "src/actions/accountActions";
import {useTranslation} from "react-i18next";
import { TRANSLATE_NS } from 'src/constants';
import { yupResolver } from '@hookform/resolvers';
import * as yup from 'yup';
import { Controller, useForm } from 'react-hook-form';
import { useHistory } from 'react-router-dom';
import { arcAction } from 'src/utils/NotificationUtil';

const useStyles = makeStyles((theme)=> ({
  root: {
    backgroundColor: theme.palette.background.default,
  },
  paper: {
    paddingTop: '3rem',
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center'
  },
  text__header: {
    marginTop: theme.spacing(8),
    fontStyle: 'italic'
  },
  text: {
    marginTop: theme.spacing(3),
    textAlign: 'center'
  },
  btn__sendEmail:{
    marginTop: theme.spacing(2)
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  errorWrapper: {
    backgroundColor: colors.red[100],
    padding: theme.spacing(2),
    margin: theme.spacing(2, 0)
  },
}));

const ForgotPassword = () => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const history = useHistory();
  const {t} = useTranslation([TRANSLATE_NS.TRANSLATIONS]);
  const [result, setResult] = useState(null);

  const schema = yup.object().shape({
    email: yup.string().email(t('translations:must be a valid email')).required(t('translations:required')),
  })

  const { control, errors, handleSubmit, formState } = useForm({
    mode: "onChange",
    defaultValues: {
      email: '',
    },    
    resolver: yupResolver(schema)
  })

  const { isSubmitting, isValid } = formState
  
  const onSubmit = handleSubmit(async (data) => {
    
    try{
      console.log('isValid: ', isValid);
      if(!isValid){
        return;
      }
      const email = data;
      await dispatch(forgotPassWordAction(email))
      arcAction.success(t('translations:email has been sent successfully'))
      history.push('/')
    }
    catch(error){
      console.log(error);
      setResult(()=> error);
      arcAction.errorObj(error);
    }
  })

  const hasError = inputName => !!(errors && errors[inputName])
  const errorMessage = inputName => errors[inputName]?.message ?? null

  return (
    <Container component="main" maxWidth="xs">
    <CssBaseline />
      <div className={classes.paper}>
         <ForgotPasswordImg />

        <Typography variant="h3" className={classes.text__header}>
        {t('translations:forgot password')}
        </Typography>

        <Typography component="span" className={classes.text} paragraph>
        {t('translations:enter email to reset password')}
        </Typography>
        <form className={classes.form}
          noValidate
          onSubmit={onSubmit}
        >

        {/* Error form API */}
              {result && (
          <Paper 
            elevation={0}
            className={classes.errorWrapper}
          >
            <Typography variant="body2" color="error">
              {result?.data?.errorMessage}
            </Typography>
          </Paper>
        )}

        {/*Email part */}
          <Controller            
            name={"email"}
            control={control}
            render={(props) => (
              <TextField
                variant="outlined"
                fullWidth
                label={t('translations:email')}
                value={props.value}
                {...props}
              />
            )}
          />
          {/*Validation for Email*/}
          {hasError("email") && (
            <div>
              <Typography variant="body2" color="error">
                {errorMessage("email")}
              </Typography>
            </div>
          )}
          {/* Send email button part */}
          <Button
            disabled={isSubmitting}
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.btn__sendEmail}
          >
            {t('translations:send email')}
          </Button>
          <Grid item xs>
            <Box display="flex" justifyContent="center">
              <Button
                size="small"
                component={Link}
                to="/login"
                className={classes.btn__sendEmail}
              >
              {t('translations:back to login')}
              </Button>
            </Box>
          </Grid>
        </form>

        <Grid item xs style={{ paddingTop: 80 }}>
          <Button
              size="small"
              variant="contained"
              component={Link}
              to="/reset-password"
            >
            Test New Password
          </Button>
        </Grid>
      </div>

    </Container>
  );
};
export default ForgotPassword;
